.description1 {
  margin-top: 20px;
  font-weight: 100;
  font-size: 25px;
}

.description2 {
  font-weight: 100;
  font-size: 16px;
}

.cloudIcon {
  margin-top: 20px;
  height: 132.84px;
  width: 201px;
}

.navigateButton {
  background-color: #13ad83;
  width: 82px;
  height: 36px;
  padding: 10px 24px;
  font-weight: 400;
  font-size: 12px;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
}

@media (min-width: 515px) {
  .component {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .cloudIcon {
    margin-top: 50px;
    height: 207.91px;
    width: 314.58px;
  }

  .description1 {
    font-size: 30px;
  }

  .description2 {
    font-size: 21px;
  }
}

@media (max-width: 514px) {
  .component {
    margin-top: 60px;
  }
}

.logo {
  height: 75px;
}
